/* HOME PAGE */
/* Hero section */
@media screen and (min-width: 992px) and (max-width: 1360px) {
    .section_hp_hero {
        align-items: flex-start;
    }
    .hp_hero_content {
        width: 80vw;
        flex-direction: column;
        flex-wrap: nowrap;
    }
    .hp_hero_right-kiwi-wrapper {
        height: 75%;
    }
    .hp_hero_right-col {
        margin-top: -20rem;
    }
    .hp_hero_arrow-wrapper.is-left-col {
        display: none;
    }
    .hp_hero_arrow-wrapper.is-right-col {
        display: none;
    }
    .hp_hero_grp-img-wrap {
        top: 6.3rem;
    }
}

/* Solutions section */
@media screen and (min-width: 992px) and (max-width: 1157px) {
    .hp_solutions_decorative-wrapper {
        top: -6.25rem;
    }
}

/* Take care section */
@media screen and (min-width: 992px) and (max-width: 1134px) {
    .w-layout-grid.hp_take-care_grid {
        display: flex;
        flex-direction: column;
    }
}

/* HIW Section */
@media screen and (min-width: 992px) and (max-width: 1241px) {
    .hp_hiw_content {
        height: auto;
        margin-bottom: var(--size--0rem);
        flex-direction: column;
        justify-content: space-between;
        flex-wrap: nowrap;
        align-items: center;
        grid-column-gap: var(--size--5rem);
        grid-row-gap: var(--size--5rem);
    }
    .hp_hiw_left-col {
        max-width: none;
    }
    .hp_hiw_right-col {
        width: 100%;
        height: 25rem;
        min-width: 0rem;
    }
    .hp_hiw_img {
        width: 100%;
        object-fit: scale-down;
        object-position: 50% 0%;
    }
    .hp_hiw_card-wrapper.is-mobile {
        display: block;
        position: absolute;
        left: auto;
        top: 37%;
        right: -1%;
        bottom: auto;
        z-index: 1;
        display: block;
        min-width: 25rem;
        transform: rotate(9deg);
    }
    .hp_hiw_card-wrapper.is-desktop img {
        display: none;
    }
    .hp_hiw_content {
        margin-bottom: 5rem;
    }
    .hp_hiw_yellow-kiwi-wrap {
        display: none;
    }
}